import { defineStore } from 'pinia'

export const useNovelStore = defineStore('novelState', {
  // arrow function recommended for full type inference
  state: () => {
    return {
      // all these properties will have their type inferred automatically
      currentNovel: null,
      novels: [
        {
          "title": "111",
          "cover": "/availableNovels/111-cover.png",
          "path": "../../public/availableNovels/111.txt",
          "content" : null
        }
      ]
    }
  },
  actions: {
    async setCurrentNovel(text) {
      this.currentNovel = text;
    },
    setNovelContent(index, content) {
      this.novels[index].content = content;
    },
  },
})