<template>
  <div class="community"> 
    <CommunityTutorial></CommunityTutorial>
    <header class="header" v-if="store.btnToggle">
      <h1 class="h1">Mulit-Author Interactive Novel Reader</h1>
    </header>
    <header class="header">
      <h2>Popular Novels</h2>
      <button class="button" @click="importFile()">Submit</button>
      <input type="file" id="file-input" style="display: none" @change="onFileInputChange">
    </header>
    <br />
    <div class="novels-container">

      <div class="novel-item" v-for="(novel, index) in this.novelStore.novels" :key="novel.title">
        <div>
          <router-link :to="{
            name: 'reader',
            query: { startReading: true },
          }" @click.prevent="readNovel(index)">
            <img :src="novel.cover" :alt="novel.title" class="novel-cover" />
          </router-link>
        </div>
        <h3>{{ novel.title }}</h3>
      </div>
    </div>
  </div>
</template>

<script>
import { useNovelStore } from "../stores/novelStore";
import { useStore } from "../stores/store";
import { app } from "../../firebaseConfig";
import { getStorage, ref } from "firebase/storage";
import { uploadBytesResumable, getDownloadURL } from 'firebase/storage';
import { useUserStore } from '../stores/userStore'; 
import CommunityTutorial from '../components/tutorials/CommunityTutorial.vue';
export default {
  components: {
    CommunityTutorial,
},
  setup() {
    const novelStore = useNovelStore();
    const store = useStore();
    const user = useUserStore(); 

    const storage = getStorage(app);
    const storageRef = ref(storage);
    const imagesRef = ref(storageRef, 'Novels');
    const fileName = 'Azeria.txt';
    const spaceRef = ref(imagesRef, fileName);
    const path = spaceRef.fullPath;
    const name = spaceRef.name;
    console.log(path, name);

    return { user, store, novelStore };
  },
  data() {
    const textFilesContext = require.context(
      "../../public/availableNovels/",
      true,
      /\.txt$/
    );
    const textFiles = textFilesContext.keys().map(textFilesContext);
    return {
      textFiles,
    };
  },
  mounted() {
    this.textFiles.forEach((textFileContent, index) => {
      this.novelStore.setNovelContent(index, textFileContent);
    });
  },
  methods: {
    async uploadFileToFirebase(file) {
      const storage = getStorage(app);
      const storageRef = ref(storage, `Novels/${file.name}`);
      const token = this.user.userInfo.token;
      const uploadTask = uploadBytesResumable(storageRef, file, {
        // 包含身份验证令牌的自定义元数据
        customMetadata: {
          'firebaseAuth': token,
        }
      });
      uploadTask.on('state_changed',
        (snapshot) => {
          // Observe state change events such as progress, pause, and resume
          // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
          const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log('Upload is ' + progress + '% done');
          switch (snapshot.state) {
            case 'paused':
              console.log('Upload is paused');
              break;
            case 'running':
              console.log('Upload is running');
              break;
            default:
              break;
          }
        },
        (error) => {
          // Handle unsuccessful uploads
          console.error('Upload failed:', error);
          alert("上传到服务器失败，请检查是否在登录状态")
        },
        async () => {
          // Handle successful uploads on complete
          const downloadURL = await getDownloadURL(storageRef);
          console.log('File available at', downloadURL);
          // Call a function to handle the file URL, e.g., save it in your database
          // handleFileURL(downloadURL);
        }
      );
    },

    async readNovel(index) {
      const novelContent = this.novelStore.novels[index].content;
      const text = typeof novelContent === 'string' ? novelContent : novelContent.default;
      console.log(text);
      await this.novelStore.setCurrentNovel(text);
      this.$router.push({
        name: "reader",
        query: {
          startReading: true,
        },
      });
    },

    importFile() {
      document.getElementById("file-input").click();
    },
    onFileInputChange(event) {
      const file = event.target.files[0];
      this.uploadFileToFirebase(file); // 添加此行以上传文件到 Firebase Storage
      const reader = new FileReader();
      reader.onload = (event) => {
        const text = event.target.result;
        this.parseNovelText(text);
      };
      reader.readAsText(file);
    },

    parseNovelText(text) {
      const lines = text.trim().split('\n');
      let title = '';
      //let author = '';
      //let content = '';
      //let l = 0;
      this.store.btnToggle = !this.store.btnToggle;
      if (lines[0].startsWith('Story Title: ')) {
        title = lines[0].slice(13);
        this.novelStore.novels.push({
          "title": title,
          "cover": "/availableNovels/111-cover.png",
          "content": text
        })
      } else {
        alert("请导入此网站所使用格式的文本文件")
      }


    }
  },
};
</script>

<style>
.community {
  text-align: left;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

.novels-container {
  margin-left: 5em;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  gap: 16px;
}

.novel-item>div {
  display: inline-block;
  text-align: center;
}

.novel-cover {
  width: 100px;
  height: auto;
}
</style>
